"use client"; 

import { useState } from "react";
import Image from "next/image";
import { Search, X, MessageCircle, MapPin, BadgeCheck, ShoppingBag, Plus, Trash2, ChevronRight } from "lucide-react";
import { Store, Product } from "@/types";

interface StoreFrontProps {
  store: Store;
  products: Product[];
}

export default function StoreFront({ store, products }: StoreFrontProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [cart, setCart] = useState<{ product: Product; qty: number }[]>([]);
  
  // Checkout State
  const [isCheckoutOpen, setIsCheckoutOpen] = useState(false);
  const [customer, setCustomer] = useState({ name: "", phone: "", address: "", email: "" });

  // Filter products
  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  // --- CART ACTIONS ---
  const addToCart = (product: Product, e?: React.MouseEvent) => {
    if (e) e.stopPropagation(); // Stop the modal from opening if clicking the "+" button
    
    setCart((prev) => {
      const existing = prev.find((item) => item.product.id === product.id);
      if (existing) {
        return prev.map((item) =>
          item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item
        );
      }
      return [...prev, { product, qty: 1 }];
    });
    
    if (selectedProduct) setSelectedProduct(null); // Close modal if open
  };

  const removeFromCart = (productId: string) => {
    setCart((prev) => prev.filter((item) => item.product.id !== productId));
  };

  const cartTotal = cart.reduce((sum, item) => sum + item.product.price * item.qty, 0);
  const cartCount = cart.reduce((sum, item) => sum + item.qty, 0);

  // --- THE WHATSAPP MAGIC ---
  const handleWhatsAppCheckout = (e: React.FormEvent) => {
    e.preventDefault(); // Stop page refresh

    // 1. Format the Order List
    const orderItems = cart
      .map((item) => `- ${item.qty}x ${item.product.name} (â‚¦${(item.product.price * item.qty).toLocaleString()})`)
      .join("\n");

    // 2. Construct the Message
    const message = `Hello ${store.name}! ðŸ‘‹\nI want to place an order:\n\n${orderItems}\n\n*Total: â‚¦${cartTotal.toLocaleString()}*\n\n*Customer Details:*\nName: ${customer.name}\nPhone: ${customer.phone}\nAddress: ${customer.address}\nEmail: ${customer.email} \n\nKindly provide me with the account detials to make payments and expected delivery time and date...`;

    // 3. Create the Link
    const whatsappUrl = `https://wa.me/${store.whatsapp_number}?text=${encodeURIComponent(message)}`;

    // 4. Send them away!
    window.open(whatsappUrl, "_blank");
  };

  return (
    <div className="bg-gray-50 min-h-screen font-sans pb-32 relative">
      
      {/* --- HEADER --- */}
      <div className="bg-white shadow-sm pb-4 rounded-b-3xl relative z-10">
        <div className="h-24 bg-gray-900 w-full rounded-b-2xl absolute top-0 left-0" />
        
        <div className="px-5 pt-12 relative">
          <div className="flex justify-between items-end">
            {/* Logo */}
            <div className="w-20 h-20 bg-white rounded-full p-1 shadow-lg">
              <div className="w-full h-full bg-gray-100 rounded-full overflow-hidden relative">
                {store.logo_url ? (
                  <Image src={store.logo_url} alt={store.name} fill className="object-cover" />
                ) : (
                  <div className="flex items-center justify-center h-full text-xl font-bold text-gray-400">
                    {store.name.charAt(0)}
                  </div>
                )}
              </div>
            </div>
            
            {/* Top Right Actions (Cart Icon) */}
            <div className="flex gap-2 mb-2">
                 <button 
                   onClick={() => setIsCheckoutOpen(true)}
                   className="relative bg-white p-2.5 rounded-full shadow-md text-gray-700 hover:bg-gray-50 transition"
                 >
                   <ShoppingBag size={20} />
                   {cartCount > 0 && (
                     <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-5 w-5 flex items-center justify-center rounded-full border-2 border-white">
                       {cartCount}
                     </span>
                   )}
                 </button>
            </div>
          </div>

          <div className="mt-3">
             <div className="flex items-center gap-1">
                <h1 className="text-2xl font-bold text-gray-900">{store.name}</h1>
                <BadgeCheck className="text-blue-500 fill-blue-50 w-6 h-6" />
             </div>
             <p className="text-gray-500 text-sm flex items-center gap-1 mt-1">
               <MapPin size={14} /> {store.location} â€¢ {store.category}
             </p>
          </div>

          {/* Search Bar */}
          <div className="mt-5 relative">
            <Search className="absolute left-3 top-3 text-gray-400 w-5 h-5" />
            <input 
              type="text" 
              placeholder={`Search ${store.name}...`}
              className="w-full bg-gray-100 pl-10 pr-4 py-3 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-gray-900 transition"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
        </div>
      </div>

      {/* --- PRODUCT GRID --- */}
      <main className="px-4 py-6">
        {filteredProducts.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {filteredProducts.map((product) => (
              <div 
                key={product.id} 
                onClick={() => setSelectedProduct(product)}
                className="bg-white p-2 rounded-2xl shadow-sm border border-gray-100 active:scale-95 transition cursor-pointer hover:shadow-md group relative"
              >
                {/* Product Image */}
                <div className="aspect-square bg-gray-100 rounded-xl mb-3 relative overflow-hidden">
                   {product.image_url ? (
                     <Image src={product.image_url} alt={product.name} fill className="object-cover" />
                   ) : (
                     <div className="w-full h-full flex items-center justify-center text-gray-300">
                        <ShoppingBag className="opacity-20" />
                     </div>
                   )}
                   
                   {/* QUICK ADD BUTTON (Bottom Right of Image) */}
                   <button 
                     onClick={(e) => addToCart(product, e)}
                     className="absolute bottom-2 right-2 bg-white text-gray-900 p-2 rounded-full shadow-lg hover:bg-emerald-50 active:scale-90 transition z-10"
                   >
                     <Plus size={16} strokeWidth={3} />
                   </button>
                </div>

                <div className="px-1 mb-2">
                   <h3 className="font-semibold text-gray-900 text-sm truncate">{product.name}</h3>
                   <p className="text-emerald-700 font-bold text-sm">â‚¦{product.price.toLocaleString()}</p>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-20 text-gray-400">
            <p>No items found.</p>
          </div>
        )}
      </main>

      {/* --- STICKY CART BAR --- */}
      {cart.length > 0 && !isCheckoutOpen && (
        <div className="fixed bottom-5 left-4 right-4 bg-gray-900 text-white p-4 rounded-2xl shadow-2xl flex justify-between items-center z-40 animate-in slide-in-from-bottom-5">
           <div className="flex flex-col">
              <span className="text-xs text-gray-400">{cartCount} Items</span>
              <span className="font-bold text-lg">â‚¦{cartTotal.toLocaleString()}</span>
           </div>
           <button 
             onClick={() => setIsCheckoutOpen(true)}
             className="bg-white text-gray-900 px-6 py-2 rounded-xl font-bold text-sm hover:bg-gray-100 transition flex items-center gap-2"
           >
             Checkout <ChevronRight size={16} />
           </button>
        </div>
      )}

      {/* --- PRODUCT DETAIL MODAL --- */}
      {selectedProduct && (
        <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
          <div className="bg-white w-full max-w-sm rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10">
            <div className="relative h-72 bg-gray-100">
               {selectedProduct.image_url && (
                 <Image src={selectedProduct.image_url} alt={selectedProduct.name} fill className="object-cover" />
               )}
               <button onClick={() => setSelectedProduct(null)} className="absolute top-4 right-4 bg-white/80 p-2 rounded-full shadow-sm">
                 <X size={20} />
               </button>
            </div>
            <div className="p-6">
              <h2 className="text-2xl font-bold text-gray-900">{selectedProduct.name}</h2>
              <p className="text-xl font-bold text-emerald-600 mt-1">â‚¦{selectedProduct.price.toLocaleString()}</p>
              <p className="text-gray-500 text-sm mt-4 leading-relaxed min-h-[60px]">
                {selectedProduct.description || "No description."}
              </p>
              <div className="mt-6">
                 <button onClick={() => addToCart(selectedProduct)} className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold shadow-lg active:scale-95 transition">
                   Add to Bag
                 </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* --- CHECKOUT FORM MODAL --- */}
      {isCheckoutOpen && (
        <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
          <div className="bg-white w-full max-w-md rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10 flex flex-col max-h-[90vh]">
            
            {/* Header */}
            <div className="p-5 border-b border-gray-100 flex justify-between items-center bg-gray-50">
              <h2 className="font-bold text-lg text-gray-900">Your Bag ({cartCount})</h2>
              <button onClick={() => setIsCheckoutOpen(false)} className="p-2 bg-white rounded-full shadow-sm text-gray-500">
                <X size={20} />
              </button>
            </div>

            {/* Scrollable Cart Items */}
            <div className="p-5 overflow-y-auto flex-1">
              {cart.map((item) => (
                <div key={item.product.id} className="flex justify-between items-center mb-4">
                  <div className="flex gap-3">
                    <div className="h-12 w-12 bg-gray-100 rounded-lg relative overflow-hidden">
                      {item.product.image_url && <Image src={item.product.image_url} alt="" fill className="object-cover" />}
                    </div>
                    <div>
                      <p className="font-medium text-sm text-gray-900">{item.product.name}</p>
                      <p className="text-xs text-gray-500">{item.qty} x â‚¦{item.product.price.toLocaleString()}</p>
                    </div>
                  </div>
                  <button onClick={() => removeFromCart(item.product.id)} className="text-red-400 p-2">
                    <Trash2 size={16} />
                  </button>
                </div>
              ))}
              
              <div className="border-t border-gray-100 pt-4 mt-4 flex justify-between items-center">
                <span className="font-bold text-gray-900">Total</span>
                <span className="font-bold text-xl text-emerald-600">â‚¦{cartTotal.toLocaleString()}</span>
              </div>
            </div>

            {/* Customer Form */}
            <div className="p-5 bg-gray-50 border-t border-gray-100">
              <h3 className="text-sm font-bold text-gray-900 mb-3">Delivery Details</h3>
              <form onSubmit={handleWhatsAppCheckout} className="space-y-3">
                <input 
                  required 
                  placeholder="Your Name" 
                  className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
                  value={customer.name}
                  onChange={e => setCustomer({...customer, name: e.target.value})}
                />
                <input 
                  required 
                  placeholder="Phone Number" 
                  type="tel"
                  className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
                  value={customer.phone}
                  onChange={e => setCustomer({...customer, phone: e.target.value})}
                />
                <input 
                  required 
                  placeholder="Delivery Address" 
                  className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
                  value={customer.address}
                  onChange={e => setCustomer({...customer, address: e.target.value})}
                />
                
                <button 
                  type="submit" 
                  disabled={cart.length === 0}
                  className="w-full bg-green-600 text-white py-4 rounded-xl font-bold shadow-lg hover:bg-green-700 active:scale-95 transition mt-2 flex items-center justify-center gap-2"
                >
                  <MessageCircle size={20} />
                  Order on WhatsApp
                </button>
              </form>
            </div>

          </div>
        </div>
      )}

    </div>
  );
}